/* container */

@import url('https://fonts.googleapis.com/css2?family=Patrick+Hand&display=swap');
.sticky-note { font-family: 'Patrick Hand', cursive; }



.paperclub {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  user-select: none;
}

.bg-layer {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  transition: opacity 1s ease-in-out;
  z-index: -1;
}

.fade-out {
  opacity: 0;
}



/* centered, smaller sticky note */
.sticky-note {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 350px;
  height: 350px;
  background: linear-gradient(to bottom, #ffef5b, #ffeb3b);
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.651);
  border: 1px solid #f5d30e;
  transform: translate(-50%, -50%);
  z-index: 900;
  cursor: pointer;
    /* standard */
  user-select: none;
  /* vendor prefixes for wider support */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;

}

/* falling notes */
.falling-note {
  position: absolute;
  top: -40px;
  font-size: 0.8rem;
  font-weight: bold;
  color: #21ca0b;
  animation-name: float;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes float {
  0% {
    transform: translateY(-20px) rotate(0deg);
    opacity: 1;
  }
  100% {
    transform: translateY(100vh) rotate(360deg);
    opacity: 0;
  }
}


/* make the container relative so children can absolutely position */
.notebox {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;  /* vertical centering of note-text */
  align-items: center;      /* horizontal centering of note-text */
  width: 100%;
  height: 100%;
  padding: 16px;
  box-sizing: border-box;
}

/* pin the count in the top-right */
.note-count {
  position: absolute;
  top: 12px;
  left: 12px;
  font-size: 40px;
  font-weight: bold;
  color: #333;
  user-select: none;
}

/* center your body copy */
.note-text {
  font-size: 29px;
  font-weight: bold;
  line-height: 1.3;
  text-align: center;
  color: #333;
  user-select: none;
  /* optional max-width to avoid super-wide lines */
  max-width: 90%;
}


@keyframes pop-shake {
  0% {
    transform: scale(1) rotate(0deg);
  }
  25% {
    transform: scale(1.2) rotate(2deg);
  }
  50% {
    transform: scale(1.1) rotate(-2deg);
  }
  75% {
    transform: scale(1.2) rotate(1deg);
  }
  100% {
    transform: scale(1) rotate(0deg);
  }
}

.animate-count {
  animation: pop-shake 0.5s ease-in-out;
}



.peeled-note {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 350px;
  height: 350px;
  background: linear-gradient(to bottom, #ffef5b, #ffeb3b);
  box-shadow: 6px 6px 12px rgba(0, 0, 0, 0.4);
  border: 1px solid #f5d30e;
  transform: translate(-50%, -50%) rotate(-8deg) scale(1.1);
  animation: peel-off 0.8s forwards ease-in;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.peeled-note .note-count {
  position: static; /* Remove absolute positioning */
  font-size: 60px;
  color: #333;
  font-weight: bold;
  text-align: center;
}


@keyframes peel-off {
  0% {
    transform: translate(-50%, -50%) rotate(0deg) scale(1);
    opacity: 1;
  }
  20% {
    transform: translate(-55%, -52%) rotate(-5deg) scale(1.05);
  }
  50% {
    transform: translate(-65%, -60%) rotate(-10deg) scale(1.1);
  }
  80% {
    transform: translate(-75%, -90%) rotate(-20deg) scale(1.1);
    opacity: 0.6;
  }
  100% {
    transform: translate(-85%, -130%) rotate(-30deg) scale(1.15);
    opacity: 0;
  }
}




.header-bar {
  position: absolute;
  top: 12px;
  left: 0;
  right: 0;
  padding: 8px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  z-index: 1001;
}

.contract-box {
  display: flex;
  align-items: center;
  padding: 10px 14px;
  background: rgba(255, 255, 255, 0.733);
  border-radius: 16px;
  backdrop-filter: blur(10px);
  font-family: sans-serif;
  font-size: 14px;
  color: #000;
}

.contract-icon {
  width: 16px;
  height: 16px;
  margin-right: 8px;
  border-radius: 50%;
  background: radial-gradient(circle at center, #0ff, #f0f);
}

.contract-text {
  white-space: nowrap;          /* Prevent wrapping */
  overflow: hidden;             /* Hide overflow */
  text-overflow: ellipsis;      /* Show ... when text overflows */
  max-width: 150px;             /* Limit max width */
  display: inline-block;
  vertical-align: middle;
  user-select: none;
}

@media (max-width: 600px) {
  .contract-text {
    max-width: 100px;           /* Even smaller on small screens */
    font-size: 12px;            /* Adjust font size for mobile */
  }
}

.about-button {
  padding: 10px 14px;
  background: rgba(255, 255, 255, 0.733);
  border: none;
  border-radius: 16px;
  font-weight: bold;
  cursor: pointer;
  backdrop-filter: blur(10px);
  color: #000;
  font-size: 14px;
    margin-right: 8px;
}

.header-icons {
  display: flex;
  gap: 14px;
  font-size: 18px;
  color: #000;
}

.icon-close,
.icon-telegram {
  cursor: pointer;
  background: rgba(255, 255, 255, 0.2);
  padding: 8px;
  border-radius: 50%;
  backdrop-filter: blur(10px);
}


.icon-twitter {
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.733);
  padding: 8px;
  border-radius: 50%;
  backdrop-filter: blur(10px);
  color: black;
  text-decoration: none;
  transition: transform 0.2s ease;
}

.icon-twitter:hover {
  transform: scale(1.1);
}




.signature-image {
  position: absolute;
  bottom: 8px;
  right: 12px;
  width: 80px; /* or adjust to fit your layout */
  opacity: 0.85;
}






.leaderboard-container {
  position: absolute;
  bottom: 16px;
  left: 50%;
  transform: translateX(-50%);
  width: 90%;
  max-width: 600px;
  font-family: sans-serif;
  z-index: 1000;
}

.leaderboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.473);
  padding: 12px 20px;
  border-radius: 12px;
  backdrop-filter: blur(10px);
  color: #000;
  font-weight: bold;
  cursor: pointer;
}

.leaderboard-arrow {
  margin-left: 12px;
}

.leaderboard-panel {
  background: rgba(255, 255, 255, 0.473);
  backdrop-filter: blur(10px);
  margin-top: 4px;
  border-radius: 12px;
  padding: 10px 0;
  max-height: 300px;
  overflow-y: auto;
}

.leaderboard-title {
  text-align: center;
  font-weight: bold;
  padding: 8px 0;
  border-bottom: 1px solid rgba(0,0,0,0.1);
}

.leaderboard-list {
  display: flex;
  flex-direction: column;
  padding: 0 16px;
}

.leaderboard-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px 0;
  font-size: 14px;
  border-bottom: 1px solid rgba(0,0,0,0.05);
  align-items: center;
}

@media (min-width: 601px) {
  .desktop-hide {
    display: none;
  }
}


.medal {
  width: 20px;
  text-align: center;
  color: #363636;
}

.flag {
  width: 30px;
  font-size: 18px;
  text-align: center;
  margin-top: -3px;
}

.country {
  flex: 1;
  margin-left: 4px;
}

.pops {
  font-weight: bold;
  text-align: right;
}






/* Modal overlay background */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
}

/* Modal content box */
.modal-content {
  background: #fff9c4;
  border-radius: 16px;
  padding: 24px 32px;
  max-width: 400px;
  width: 90%;
  text-align: center;
  position: relative;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.3);
  font-family: 'Patrick Hand', cursive;
  animation: pop-in 0.25s ease-out;
  box-sizing: border-box;
}

/* Pop-in animation */
@keyframes pop-in {
  0% {
    transform: scale(0.9);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Close button */
.modal-close {
 

  background: transparent !important;
  border: none;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  color: #333;
  display: flex !important;
  justify-content: right !important;
  width: 100% !important;
  align-items: right;
}

/* Buttons in modal */
.modal-content button {
  margin-top: 0px;
  margin-bottom: 8px;
  padding: 10px 16px;
  border: none;
  background: #ffd600;
  color: #000;
  font-weight: bold;
  font-size: 16px;
  border-radius: 10px;
  cursor: pointer;
  transition: background 0.2s ease-in-out;
  display: block;
  width: 100%;
}

.modal-content button:hover {
  background: #ffea00;
}

/* Address text */
.modal-content p {
  font-family: monospace;
  font-size: 15px;
  word-break: break-all;
  color: #333;
  margin: 16px 0;
}







.logoimage {
